<template>
  <div class="container">
    <div class="form">
      <el-form label-width="120px" :inline="true" :model="lifeTableDetail" ref="lifeTableDetailForm">
        <el-form-item label="机构代码:" prop="orgCode">
          <el-select placeholder="请选择机构" v-model="lifeTableDetail.orgCode">
            <el-option
              v-for="org in orgList"
              :key="org.orgCode"
              :label="org.orgName"
              :value="org.orgCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="报价策略渠道:" prop="channel">
          <el-select placeholder="请选择渠道" v-model="lifeTableDetail.channel">
            <el-option
              v-for="channel in channelList"
              :key="channel.channelCode"
              :label="channel.channelName"
              :value="channel.channelCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="版本号:" prop="version">
          <el-input readonly v-model="lifeTableDetail.version">
          </el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>

</template>

<script>
export default {
  name: 'LifeTableQueryForm',
  props: {
    lifeTableDetail: {
      required: true
    }
  },
  data () {
    return {
      orgList: [
        {orgCode: '000000', orgName: '北京分公司'},
        {orgCode: '000001', orgName: '天津分公司'},
        {orgCode: '000002', orgName: '上海分公司'}
      ],
      channelList: [
        {channelCode: '001', channelName: '保险公司车险'},
        {channelCode: '002', channelName: '银行邮政代理'},
        {channelCode: '003', channelName: '电话车险'},
        {channelCode: '004', channelName: '网销车险'},
        {channelCode: '005', channelName: '4S店保险中心车险'},
        {channelCode: '006', channelName: '维修店专业车险'}
      ]
    }
  }
}
</script>

<style scoped>

</style>



